html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #EF097E;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

html {
  scroll-behavior: smooth; }

.contenedor__Header {
  background: linear-gradient(#FFFFFF, transparent);
  z-index: 1;
  width: 100%;
  position: fixed; }
  .contenedor__Header .header {
    margin: 0 auto;
    max-width: 125rem;
    width: 98%;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .contenedor__Header .header .logo {
      width: 8rem;
      height: 5rem;
      background-image: url("../img/logo-01.png");
      background-size: 6rem;
      background-repeat: no-repeat;
      background-position: center center;
      margin-left: -1rem; }
    .contenedor__Header .header .nav {
      display: flex;
      column-gap: 1rem;
      margin-right: 1rem; }
      .contenedor__Header .header .nav a {
        color: #1a1b15; }
        .contenedor__Header .header .nav a:hover {
          text-decoration: underline;
          color: #cf056d; }

.banner {
  width: 100%;
  min-height: 100vh;
  background-image: url("../img/m.webp");
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .banner h1 {
    color: #0694BA;
    font-size: 7rem;
    margin-bottom: 1rem;
    text-align: left;
    margin-left: 2rem; }
  .banner h2 {
    text-align: left;
    margin: 0 0 2rem 3rem; }
  .banner .lema {
    padding: 1rem 3rem 1rem 0;
    background-color: #EF097E;
    max-width: 50rem; }
    .banner .lema h3 {
      margin: 0;
      font-size: 3rem;
      color: #FFFFFF;
      text-align: right; }

.conocenos {
  min-height: 100vh;
  display: flex;
  align-items: center; }
  .conocenos .div_nosotros {
    background-color: #0694BA;
    width: 100%; }
    @media (min-width: 768px) {
      .conocenos .div_nosotros .nosotros {
        display: grid;
        gap: 5rem;
        grid-template-columns: repeat(2, 1fr);
        max-width: 120rem; } }
    .conocenos .div_nosotros .nosotros .logo {
      background-color: #FFFFFF;
      height: 25rem;
      width: 25rem;
      border-radius: 50%;
      margin: 4rem auto; }
      @media (min-width: 768px) {
        .conocenos .div_nosotros .nosotros .logo {
          height: 33rem;
          width: 33rem; } }
    .conocenos .div_nosotros .nosotros .presentacion {
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .conocenos .div_nosotros .nosotros .presentacion h3 {
        color: #FFFFFF;
        font-size: 4rem;
        text-align: left;
        margin-left: 2rem; }
      .conocenos .div_nosotros .nosotros .presentacion p {
        margin: 1rem 2rem;
        font-size: 2.4rem; }
        .conocenos .div_nosotros .nosotros .presentacion p:last-child {
          margin-bottom: 4rem; }

.galeria {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .galeria .container_galeria {
    margin: 8rem 0;
    max-width: 120rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 2rem; }
    @media (min-width: 768px) {
      .galeria .container_galeria {
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(4, 1fr); } }
    .galeria .container_galeria img {
      max-width: 100%;
      height: 25rem;
      object-fit: cover;
      transition: transform 400ms; }
      .galeria .container_galeria img:hover {
        transform: scale(1.05); }

.contendor_login {
  min-height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .contendor_login .login {
    padding: 2rem 1rem; }
    .contendor_login .login form h4 {
      color: #EF097E;
      text-align: center;
      font-size: 3rem;
      margin: 0;
      margin-bottom: 3rem; }
    .contendor_login .login form p {
      color: #EF097E;
      margin: 0;
      margin-left: 1.2rem;
      margin-top: .5rem;
      font-size: 1.7rem; }
      .contendor_login .login form p:hover {
        cursor: pointer; }
    .contendor_login .login .btn {
      width: 100%;
      font-size: 2rem;
      padding: 1rem;
      background-color: #EF097E;
      color: #FFFFFF;
      margin-top: 2rem;
      border-radius: 2.5rem;
      border: none; }
      .contendor_login .login .btn:hover {
        cursor: pointer;
        background-color: #cf056d; }
  .contendor_login .campo_login {
    display: flex;
    flex-direction: column; }
    .contendor_login .campo_login input {
      background-color: #ffffffa9;
      border: none;
      font-size: 2rem;
      padding: 1rem;
      outline: none;
      margin-bottom: 1rem;
      border-radius: 2.5rem;
      border: 2px solid #0694BA; }

.contenedor__foooter {
  background-color: #0694BA;
  margin-top: 8rem; }
  .contenedor__foooter footer {
    padding: 4rem 0;
    width: 98%;
    max-width: 120rem;
    margin: 0 auto;
    text-align: center; }
    @media (min-width: 768px) {
      .contenedor__foooter footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 2rem;
        text-align: left; } }
    .contenedor__foooter footer .contactos p {
      margin: .5rem; }
    .contenedor__foooter footer .derechos {
      max-width: 50rem; }
    .contenedor__foooter footer p {
      font-size: 1.5rem;
      font-weight: 100; }

.overlay {
  display: none; }

.overlays {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 2;
  background-color: #FFFFFF;
  display: grid;
  place-items: center; }
  @media (min-width: 768px) {
    .overlays {
      display: block;
      background: linear-gradient(90deg, white 0%, rgba(255, 255, 255, 0.835172) 46%, rgba(255, 255, 255, 0) 100%); } }
  .overlays form {
    max-width: 80%; }
    @media (min-width: 768px) {
