      padding: 1rem;
      background-color: #EF097E;
      color: #FFFFFF;
      margin-top: 2rem;
      border-radius: 2.5rem;
      border: none; }
      .overlays form .btn:hover {
        cursor: pointer;
        background-color: #cf056d; }
    .overlays form .two {
      display: grid;
      grid-template-columns: 1fr 3fr; }
      .overlays form .two label {
        margin-left: 1rem; }
  .overlays p {
    background-color: #EF097E;
    color: #FFFFFF;
    font-weight: bold;
    font-size: 2rem;
    padding: 1rem 2rem;
    border-radius: 25%;
    display: inline-block;
    margin: 1rem 2rem; }
    .overlays p:hover {
      cursor: pointer;
      background-color: #cf056d; }
